body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(117, 64, 202)), to(rgba(48, 9, 94, 0.867)));
  background-image: -webkit-linear-gradient(top, rgb(117, 64, 202), rgba(48, 9, 94, 0.867));
  background-image: -o-linear-gradient(top, rgb(117, 64, 202), rgba(48, 9, 94, 0.867));
  background-image: linear-gradient(to bottom, rgb(117, 64, 202), rgba(48, 9, 94, 0.867));
  background-repeat: repeat;
  background-size: cover;
  overflow-x: hidden;
}



